.overflow {
  overflow:hidden
}

.Parade {
  text-align: center;
  background-color: rgba(0, 0, 0, 0);;
  display: flex;
  flex-direction: row;
  position: fixed;
  bottom: 0;
  width: 100%;
  animation: infinite;
  animation-name: parade;
  animation-duration: 50s;
  animation-timing-function : linear;
}

@keyframes parade {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-1000%);
  }
}